.examples-product {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    min-height: 283px;
    position: relative;
    align-content: flex-end;
    padding-bottom: 35px;

    @media (min-width: @tablet-width) {
      min-height: 362px;
      align-content: flex-start;
      padding-bottom: 11%;
      background: #f2f2f2;
      @media (min-width: @tablet-width) and (max-width: 1339px) {
        &::before,
        &::after {
          content: "";
          width: 10vw;
          background: #f2f2f2;
          height: 100%;
          position: absolute;
          top: 0;
        }

        &::before {
          left: -30px;
        }

        &::after {
          right: -30px;
        }
      }
    }

    @media (min-width: @desktop-width) {
      width: 50%;
      padding-bottom: 0;
      align-content: flex-end;
    }
  }

  &__description {
    @media (min-width: @tablet-width) {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      min-height: 481px;
    }
    @media (min-width: @desktop-width) {
      min-height: 392px;
      width: 50%;
      padding-top: 5.7%;
      background: #f2f2f2;
    }
  }

  &__title {
    color: @black;
    font-size: 36px;
    line-height: 40px;
    margin-top: 25px;
    margin-bottom: 21px;

    @media (min-width: @tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 47px;
      margin-top: 44px;
    }
    @media (min-width: @desktop-width) {
      position: relative;
      font-size: 58px;
      margin-bottom: 0;
      margin-top: 0;
      padding-top: 47px;
      width: 100%;
      min-height: 136px;
      background-color: #fff;

      &::before,
      &::after {
        content: "";
        position: absolute;
        height: 100%;
        background-color: #ffffff;
        top: 0;
      }

      &::before {
        left: -40vw;
        width: calc(40vw);
      }

      &::after {
        width: calc(40vw);
        right: -40vw
      }
    }
  }

  &__text {
    margin-bottom: 26px;
    color: @dark-grey-second;
    font-size: 14px;
    line-height: 18px;
    font-family: "Arial";
    font-weight: @font-weight-default;
    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 24px;
      height: 20.3%;
      padding-right: 10px;
    }
    @media (min-width: @desktop-width) {
      width: 72%;
      padding: 0;
      margin: 0;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    @media (min-width: @tablet-width) {
      width: 51.7%;
      height: 70%;
      align-items: flex-start;
      padding-bottom: 76px;
    }
    @media (min-width: @desktop-width) {
      width: 60%;
      padding-bottom: 0;
      height: 23%;
      align-items: flex-start;
      margin-top: 8%;
    }
  }

  &__items {
    position: relative;
    width: 45%;
    border: 1px solid #CDCDCD;
    box-sizing: border-box;
    border-radius: 3px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    @media (min-width: @tablet-width) {
      min-width: 162px;
      padding: 0;
    }
  }

  &__item-price,
  &__item-days {
    margin-top: 14px;
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 24px;
    color: @black;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      font-size: 30px;
      line-height: 37px;
    }
  }

  &__item-text {
    position: absolute;
    bottom: -51%;
    left: 19%;
    width: 61%;
    background: #F2F2F2;
    font-family: "Arial";
    font-size: 12px;
    line-height: 12px;
    color: @dark-grey-second;
    @media (min-width: @tablet-width) {
      font-size: 14px;
      line-height: 14px;
      bottom: -36%;
      left: 6%;
      width: 87%;
      background: white;
    }
    @media (min-width: @desktop-width) {
      background: #F2F2F2;
    }
  }

  &__cost {
    margin-top: 34px;
    margin-bottom: 44px;
    font-size: 14px;
    line-height: 20px;
    color: @black;
    text-align: center;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      line-height: 28px;
      font-size: 20px;
      text-align: right;
      margin-top: 8px;
      width: 25%;
      padding-left: 23%;
      margin-bottom: 0;
    }
    @media (min-width: @desktop-width) {
      width: 100%;
      padding-left: 0;
      text-align: left;
      height: 15%;
      margin-top: 0;
      margin-bottom: 5px;
      span {
        padding-left: 59px;
      }
    }
  }

  &__cat-before,
  &__cat-after {
    position: absolute;
    top: 0;
    left: -7%;
    width: 111%;
    @media (min-width: @tablet-width) {
      top: -44%;
      left: 2%;
      width: 96%;
    }
  }

  &--before {
    @media (min-width: @mobile-width) and (max-width: 767px) {
      .before-after__green-slide {
        transition: 0.5s ease-in-out;
        left: 0;
      }
    }

    .examples-product__cat-before {
      picture {
        transition: right 0.5s ease-in-out;
      }

      img {
        transition: 0.5s ease-in-out;
        display: block;
        margin: 0 auto;
        height: 228px;
        @media (min-width: @tablet-width) {
          height: 529px;
        }
      }
    }

    .examples-product__cat-after {
      img {
        margin: 0 auto;
        display: none;
      }
    }

    .before-after__white-circle {
      transition: 0.5s ease-in-out;
      left: 0;
    }
  }

  &--after {
    @media (min-width: @mobile-width) and (max-width: 767px) {
      .before-after__green-slide {
        transition: 0.5s ease-in-out;
        left: 46%;
      }
    }

    .examples-product__cat-after {
      picture {
        transition: left 0.5s ease-in-out;
      }

      img {
        transition: 0.5s ease-in-out;
        display: block;
        margin: 0 auto;
        height: 228px;
        @media (min-width: @tablet-width) {
          height: 529px;
        }
      }
    }

    .examples-product__cat-before {
      img {
        margin: 0 auto;
        display: none;
      }
    }

    .before-after__white-circle {
      transition: 0.5s ease-in-out;
      left: 93%;
    }

    @media (min-width: @desktop-width) {
      .examples-product__container {
        transition: background 0.35s linear;
        background: #eaeaea;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          right: -50vw;
          width: 50vw;
          height: 100%;
          background: #eaeaea;
          transition: 0.35s linear;
        }
      }
    }
  }
}
