}
.navigation {
  position: relative;
  margin-top: 12px;
}
@media (min-width: 320px) and (max-width: 768px) {
  .navigation--open .navigation__button::before,
  .navigation--open .navigation__button::after {
    content: "";
    position: absolute;
    width: 2px;
    height: 25px;
    top: 2px;
    right: 10px;
    transform: rotate(45deg);
    background: #000000;
    transition: 0.25s ease-in-out;
  }
  .navigation--open .navigation__button::before {
    transform: rotate(-45deg);
  }
  .navigation--closed .menu {
    display: none;
  }
  .navigation--closed .navigation__button::before,
  .navigation--closed .navigation__button::after {
    content: "";
    position: absolute;
    background: #000000;
    transition: 0.25s ease-in-out;
    width: 22px;
    height: 2px;
    right: 10px;
    top: 8px;
  }
  .navigation--closed .navigation__button::after {
    box-shadow: 0 -6px #000000;
    top: 20px;
  }
  .navigation--no-js .navigation__button {
    display: none;
  }
  .navigation--no-js .menu {
    display: flex;
  }
}
@media (min-width: 768px) {
  .navigation {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 25px;
    margin: 0 auto;
    box-sizing: border-box;
    min-width: 320px;
    max-width: 1440px;
  }
}
