.form-header {
  @media (min-width: @tablet-width) {
    padding-left: 70px;
    padding-right: 70px;
    margin-top: 41px;
    @media (min-width: @tablet-width) {
      padding-left: 110px;
      padding-right: 110px;
      margin-top: 53px;
    }
  }

  &__title {
    margin-top: 27px;
    margin-bottom: 37px;
    font-size: 36px;
    line-height: 36px;
    @media (min-width: @tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-bottom: 55px
    }
    @media (min-width: @desktop-width) {
      font-size: 58px;
    }
  }

  &__text {
    margin-top: 0;
    margin-bottom: 25px;
    color: @black;
    font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
    width: 90%;
    @media (min-width: @tablet-width) {
      margin-bottom: 9px;
      font-size: 20px;
      line-height: 30px;
      width: 61%;
    }
    @media (min-width: @desktop-width) {
      width: 30%;
      margin-bottom: 13px;
    }
  }
}
