img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

button {
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: @second-green;
  }

  &:active {
    background-color: @second-green;
    color: fade(@color-white, 20%);
  }
}
