.adds {
  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }

  &__title {
    padding-bottom: 39px;
    @media (min-width: @tablet-width) {
      padding-bottom: 62px;
      padding-top: 9px;
    }
    @media (min-width: @desktop-width) {
      padding-top: 0;
    }

    &::before {
      content: "";
      position: absolute;
      width: 47%;
      right: -17px;
      top: 21%;
      height: 2px;
      background-color: @basic-green;
      @media (min-width: @tablet-width) {
        width: 70.57%;
        right: -65px;
        top: 28%;
      }
      @media (min-width: @desktop-width) {
        width: 70.57%;
        right: -110px;
        top: 22%;
      }
    }
  }

  &__label {
    width: 100%;
    min-height: 30px;
    margin-bottom: 12px;
    @media (min-width: @tablet-width) {
      width: 45%;
      margin-bottom: 30px;
      min-height: 42px;
    }
    @media (min-width: @desktop-width) {
      width: 20%;
      margin-bottom: 16px;
    }
  }

  &__text {
    text-align: left;
    width: 100%;
    padding-left: 16%;
    height: 100%;
    @media (min-width: @tablet-width) {
      padding-left: 20.9%;
    }
    @media (min-width: @desktop-width) {
      padding-left: 24.9%;
    }
  }

  &__input {

    + .adds__text {
      cursor: pointer;

      &:hover::before,
      &:focus::before {
        border: 1px solid #BABABA;
      }

      &:active::before,
      &:active::after {
        opacity: 30%;
      }

      &::before,
      &::after {
        content: "";
        position: absolute;
        top: -6px;
        left: 0;
      }

      &::before {
        width: 28px;
        height: 28px;
        border: 1px solid #CDCDCD;
        @media (min-width: @tablet-width) {
          width: 38px;
          height: 38px;
          top: -12%;
          left: 0;
        }
      }
    }

    &:checked + .adds__text {
      &::after {
        top: 5%;
        left: 6px;
        width: 18px;
        height: 14px;
        background-image: url("../img/icons/tick.png");
        @media (min-width: @tablet-width) {
          top: 19%;
          left: 12px;
        }
      }
    }
  }
}
