.wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 320px;
  max-width: @desktop-width;
  padding-left: 20px;
  padding-right: 20px;
  @media (min-width: @tablet-width) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @media (min-width: @desktop-width) {
    padding-left: 110px;
    padding-right: 110px;
  }
}
