.pet-programm {
  position: relative;
  border: none;
  padding-left: 0;
  padding-right: 0;
  padding-top: 5px;
  padding-bottom: 19px;
  @media (min-width: @tablet-width) {
    border: 1px solid #E7E7E7;
  }
  @media (min-width: @desktop-width) {
    padding-bottom: 10px;
    padding-top: 26px;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 1px;
    background-color: #d9d9d9;
    top: 0;
    left: 0;
    margin-left: -20px;
    @media (min-width: @tablet-width) {
      display: none;
    }
  }

  &::after {
    top: 100%;
  }

  &__label {
    width: 100%;
    min-height: 29.7px;
    margin-top: 14px;
    @media (min-width: @tablet-width) {
      margin-top: 23px;
    }
    @media (min-width: @desktop-width) {
      min-height: 38px;
      margin-top: 0;
      margin-bottom: 15px;
    }
  }

  &__text {
    text-align: left;
    width: 100%;
    padding-left: 16%;
    @media (min-width: @tablet-width) {
      padding-left: 14%;
    }
    @media (min-width: @desktop-width) {
      padding-left: 15.6%;
    }
  }

  &__input {
    + .pet-programm__text {
      cursor: pointer;

      &:hover::before,
      &:focus::before {
        border: 1px solid #BABABA;
      }

      &:active::before,
      &:active::after {
        opacity: 30%;
      }

      &::before,
      &::after {
        content: "";
        position: absolute;
        top: -6px;
        left: -1px;
        border-radius: 50%;
      }

      &::before {
        width: 30px;
        height: 30px;
        border: 1px solid #CDCDCD;
        background: transparent;

        @media (min-width: @tablet-width) {
          width: 38px;
          height: 38px;
          top: -5px;
          left: 28px;
        }
      }
    }

    &:checked + .pet-programm__text {
      &::after {
        top: 23%;
        left: 9px;
        width: 13px;
        height: 13px;
        background-color: @basic-green;

        @media (min-width: @tablet-width) {
          width: 18px;
          height: 18px;
          left: 39px;
          top: 23%;
        }
      }
    }
  }
}
