.goods {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 87px;
  }

  &__title {
    position: relative;
    margin-top: 28px;
    font-size: 24px;
    line-height: 24px;
    color: @black;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 62%;
      height: 2px;
      background-color: @basic-green;
    }

    &::before {
      right: -20px;
      width: 18.4%;
      top: 57%;
    }

    @media (min-width: @tablet-width) {
      width: 100%;
      font-size: 36px;
      line-height: 36px;
      margin-left: 36px;
      margin-top: 49px;


      &::before {
        top: 62%;
        width: 44%;
        right: 7px;
      }

      &::after {
        width: 4.24%;
        left: -66px;
      }
    }

    @media (min-width: @desktop-width) {
      font-size: 35px;
      margin-left: 0;
      margin-top: 53px;

      &::before {
        width: 78%;
        right: -105px;
      }

      &::after {
        width: 5.7%;
        left: -106px;
      }
    }
  }

  &__list {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    list-style-type: none;
    @media (min-width: @tablet-width) {
      margin-top: 50px;
    }
    @media (min-width: @desktop-width) {
      width: 73.4%;
    }
  }

  &__link {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 14px;
    margin-bottom: 2px;
    border-top: 1px solid #CDCDCD;
    border-bottom: 1px solid #CDCDCD;
    @media (min-width: @tablet-width) {
      border: 1px solid #CDCDCD;
      padding: 13px 38px 25px;
      margin: 0;
    }
    @media (min-width: @desktop-width) {
      border-left: none;
      border-right: none;
      padding: 5px 0 5px;
    }
  }

  &__name {
    margin-top: 13px;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 16px;
    color: @special-dark-grey;
    text-transform: uppercase;
    width: 100%;
    @media (min-width: @tablet-width) {
      width: 50%;
      font-size: 20px;
      line-height: 20px;
    }
    @media (min-width: @desktop-width) {
      width: 36%;
      margin-top: 20px;
    }
  }

  &__type,
  &__price {
    margin-top: 13px;
    font-family: "Arial";
    font-size: 14px;
    line-height: 14px;
    color: @special-dark-grey;
    width: 60%;
    @media (min-width: @tablet-width) {
      margin-bottom: 23px;
      font-size: 16px;
      line-height: 20px;
      width: 21%;
      text-align: right;
    }
    @media (min-width: @desktop-width) {
      width: 27%;
      text-align: left;
      margin-top: 23.6px;
      margin-bottom: 17px;
    }
  }

  &__price {
    width: 14%;
    @media (min-width: @desktop-width) {
      width: 9%;
      text-align: left;
    }
  }

  &__button {
    font-family: @font-family-base;
    color: @color-white;
    font-size: 16px;
    line-height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    text-transform: uppercase;
    border: none;
    background-color: @basic-green;
    width: 100%;
    @media (min-width: @desktop-width) {
      width: 27.2%;
      align-self: center;
    }
  }

  &__gift {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    margin-top: 55px;
    background-image: url("../img/gift-photo-mobile.png");
    background-size: cover;
    height: 289px;
    @media (min-width: @tablet-width) {
      margin-top: 71px;
      margin-bottom: 75px;
      background-size: 100%;
      flex-direction: row;
      justify-content: space-between;
      height: 200px;
      background-image: url("../img/gift-photo-tablet.png");
    }
    @media (min-width: @desktop-width) {
      width: 20%;
      margin: 50px 0 0;
      height: 289px;
      background-size: cover;
      background-image: url("../img/gift-photo-desktop.jpg");
    }

    svg {
      padding-top: 39px;
      @media (min-width: @tablet-width) {
        padding-top: 0;
        width: 33%;
      }
      @media (min-width: @desktop-width) {
        padding-top: 44px;
      }
    }
  }

  &__gift-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    @media (min-width: @tablet-width) {
      justify-content: space-between;
      flex-direction: row;
      padding-left: 19px;
      padding-right: 93px;
    }
    @media (min-width: @desktop-width) {
      flex-direction: column;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__gift-text {
    font-family: "Arial";
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: @color-white;
    width: 62%;
    margin-bottom: 25px;
    @media (min-width: @tablet-width) {
      width: 32%;
      margin-bottom: 30px;
      margin-top: 35px;
      text-align: left;
    }
    @media (min-width: @desktop-width) {
      width: 68%;
      margin-top: 49px;
      text-align: center;
    }
  }
}

