.owner-contact {
  @media (min-width: @desktop-width) {
    justify-content: space-between;
  }

  &__title {
    padding-top: 13px;
    padding-bottom: 34px;
    @media (min-width: @tablet-width) {
      padding-bottom: 79px;
      br {
        display: none;
      }
    }
    @media (min-width: @desktop-width) {
      padding-top: 0;
      padding-bottom: 75px;
    }

    &::before {
      content: "";
      position: absolute;
      width: 35%;
      right: -17px;
      top: 29%;
      height: 2px;
      background-color: @basic-green;
      @media (min-width: @tablet-width) {
        width: 23%;
        right: -65px;
        top: 29%;
      }
      @media (min-width: @desktop-width) {
        width: 63.57%;
        right: -110px;
        top: 16.4%
      }
    }
  }

  &__label {
    min-height: 45px;
    margin-bottom: 13px;
    width: 100%;
    @media (min-width: @tablet-width) {
      margin-bottom: 0;
      min-height: 50px;
      &:not(:last-child) {
        margin-bottom: 3.3%;
        @media (min-width: @desktop-width) {
          margin-bottom: 1.5%;
        }
      }
    }
    @media (min-width: @desktop-width) {
      width: 47%;
      height: 50px;
    }
  }

  &__text {
    align-self: center;
  }

  &__input {
    flex-grow: 1;
    margin-left: 13px;
    border: 1px solid #E7E7E7;
    outline: none;

    &:hover {
      border: 1px solid #CDCDCD;
    }

    &:focus,
    &:focus-within {
      border: 2px solid #444444;
    }

    @media (min-width: @tablet-width) {
      margin-left: 2.6%;
    }
    @media (min-width: @desktop-width) {
      margin-left: 3.5%;
    }
  }
}
