.menu {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  @media (min-width: @tablet-width) {
    flex-direction: row;
    width: 62%;
    justify-content: space-between;
    align-content: baseline;
  }
  @media (min-width: @desktop-width) {
    width: 39.3%;
    justify-content: space-between;
  }
}

.menu__items {
  width: 100%;
  text-align: center;
  border: 1px solid #E6E6E6;

  @media (min-width: @tablet-width) {
    border: none;
    width: max-content;
  }
}

.menu__link {
  display: block;
  font-weight: @font-weight-medium;
  font-size: 21px;
  line-height: 20px;
  text-decoration: none;
  color: @black;
  text-transform: uppercase;
  width: 100%;
  padding-top: 22px;
  padding-bottom: 20px;

  &:hover,
  &:focus {
    opacity: 60%;
  }

  &:active {
    opacity: 30%;
  }

  @media (min-width: @tablet-width) {
    font-size: 18px;
    line-height: 24px;
    padding-top: 17px;
  }
  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
    &--index {
      color: white;
    }
  }
}
