.partners {
  display: grid;
  position: relative;

  &--grey {
    background-color: #F2F2F2;
  }

  &__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 13px;
    margin-bottom: 10px;
    @media (min-width: @tablet-width) {
      padding-left: 42px;
      padding-right: 42px;
      margin-top: 46px;
      margin-bottom: 38px;
    }
    @media (min-width: @desktop-width) {
      position: absolute;
      background: @color-white;
      padding: 45px 66px 38px 74px;
      width: 29.68%;
      top: 49px;
      left: 7.7%;
    }
  }

  &__info {
    @media (min-width: @desktop-width) {
      position: relative;
      width: 100%;
    }
  }

  &__invite {
    font-size: 16px;
    line-height: 20px;
    color: @black;
    text-transform: uppercase;
    width: 45%;
    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 26px;
      width: 30%;
    }
    @media (min-width: @desktop-width) {
      width: 41%;
    }
  }

  &__address {
    font-size: 14px;
    line-height: 21px;
    font-family: "Arial";
    width: 49%;
    margin-top: 15px;

    @media (min-width: @tablet-width) {
      font-size: 16px;
      margin-left: 10px;
      margin-top: 17px;
      width: 30%;
      text-align: right;
      span {
        margin-top: 20px;
        display: inline-block;
        @media (min-width: @desktop-width) {
          margin-top: 18px;
        }
      }
    }
    @media (min-width: @desktop-width) {
      width: 41%;
      text-align: left;
      margin-top: 18px;
    }
  }

  &__map-pin {
    position: absolute;
    top: 53%;
    left: 5px;
    right: 0;
    margin: 0 auto;
    width: 53px;
    height: 53px;
    @media (min-width: @tablet-width) {
      width: 113px;
      height: 106px;
      top: 58%;
      left: 12px;
    }
    @media (min-width: @desktop-width) {
      top: 33%;
      left: 34%;
    }
  }

  &__map {
    img {
      width: 100%;
      height: 362px;
      display: flex;
      @media (min-width: @tablet-width) {
        height: 400px;
      }
    }
  }
}
