.navigation {
  position: relative;
  margin-top: 12px;
  @media (min-width: @mobile-width) and (max-width: @tablet-width) {

    &--open {
      .navigation__button {
        &::before,
        &::after {
          content: "";
          position: absolute;
          width: 2px;
          height: 25px;
          top: 2px;
          right: 10px;
          transform: rotate(45deg);
          background: @black;
          transition: 0.25s ease-in-out;
        }

        &::before {
          transform: rotate(-45deg);
        }
      }
    }

    &--closed {
      .menu {
        display: none;
      }

      .navigation__button {

        &::before,
        &::after {
          content: "";
          position: absolute;
          background: @black;
          transition: 0.25s ease-in-out;
          width: 22px;
          height: 2px;
          right: 10px;
          top: 8px;
        }

        &::after {
          box-shadow: 0 -6px @black;
          top: 20px;
        }
      }
    }

    &--no-js {
      .navigation__button {
        display: none;
      }

      .menu {
        display: flex;
      }
    }
  }
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 25px;
    margin: 0 auto;
    box-sizing: border-box;
    min-width: @mobile-width;
    max-width: @desktop-width;
  }
  @media (min-width: @desktop-width) {
    padding-left: 110px;
    padding-right: 110px;
    padding-top: 54px;
  }
}

.navigation__logo {
  text-decoration: none;
  padding-left: 26px;
  padding-bottom: 17px;
  display: block;

  img {
    width: 190px;
    height: 37px;
    @media (min-width: @tablet-width) {
      width: 174px;
      height: 51px;
    }
    @media (min-width: @desktop-width) {
      width: 202px;
      height: 59px;
    }
  }

  @media (min-width: @tablet-width) {
    padding-left: 0;
    width: 30%;
  }
}

.navigation__button {
  position: absolute;
  width: 30px;
  height: 30px;
  display: block;
  border: none;
  background: transparent;
  top: 3px;
  right: 19px;
  cursor: pointer;
  transition: all 0.25s ease;
  @media (min-width: @tablet-width) {
    display: none;
  }
}
