.form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  @media (min-width: @tablet-width) {
    padding-left: 70px;
    padding-right: 70px;
    margin-bottom: 55px;
  }
  @media (min-width: @desktop-width) {
    flex-direction: row;
    padding-left: 110px;
    padding-right: 110px;
  }

  label {
    position: relative;
    display: flex;
    flex-direction: row;
  }

  span {
    position: relative;
    text-transform: uppercase;
    align-self: center;
    font-size: 16px;
    line-height: 18px;
    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }
  }

  input {
    font-family: @font-family-base;
    font-weight: @font-weight-default;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
  }

  fieldset {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    margin-top: 14px;
    width: 100%;

    &:not(.form__programm) {
      border: none;
      padding: 0;
    }

    @media (min-width: @tablet-width) {
      margin-top: 57px;
    }
    @media (min-width: @desktop-width) {
      margin-top: 51px;
    }

    &.form__programm,
    &.form__info-pet {
      @media (min-width: @desktop-width) {
        width: 46.7%;
        height: max-content;
      }
    }
  }

  legend {
    position: relative;
    font-size: 24px;
    line-height: 26px;
    width: 100%;
    @media (min-width: @tablet-width) {
      font-size: 36px;
      line-height: 36px;
      &::after {
        content: "";
        position: absolute;
        width: 4.747%;
        left: -70px;
        top: 26%;
        height: 2px;
        background-color: @basic-green;
        @media (min-width: @desktop-width) {
          width: 5.72%;
          left: -110px;
          top: 19%;
        }
      }
    }

  }

  &__controls {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 15px;
    margin-bottom: 17px;
    width: 100%;
    @media (min-width: @desktop-width) {
      margin-top: 37px;
      margin-bottom: 26px;
    }
  }

  &__button {
    font-family: @font-family-base;
    border: none;
    background: @basic-green;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 20px;
    color: @color-white;
    width: 100%;
    @media (min-width: @tablet-width) {
      width: 46.8%;
      font-size: 20px;
    }
    @media (min-width: @desktop-width) {

    }
  }

  &__text {
    font-family: "Arial";
    font-style: normal;
    font-weight: @font-weight-medium;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    margin-top: 22px;
    width: 100%;
    @media (min-width: @tablet-width) {
      width: 48%;
      font-size: 16px;
      margin-top: 15px;
      margin-bottom: 21px;
    }
    @media (min-width: @desktop-width) {
      width: 28%;
    }
  }
}

