.owner-comment {

  &__title {
    padding-bottom: 37px;
    @media (min-width: @tablet-width) {
      padding-bottom: 77px;
      padding-top: 10px;
    }
    @media (min-width: @desktop-width) {
      padding-top: 0;
    }

    &::before {
      content: "";
      position: absolute;
      width: 54%;
      right: -17px;
      top: 21%;
      height: 2px;
      background-color: @basic-green;
      @media (min-width: @tablet-width) {
        width: 75.65%;
        right: -65px;
        top: 26.4%;
      }
      @media (min-width: @desktop-width) {
        width: 88.072%;
        right: -110px;
        top: 19.4%;
      }
    }
  }

  &__label {
    width: 100%;

    textarea {
      font-family: @font-family-base;
      font-weight: @font-weight-default;
      color: @black;
      padding: 9px 12px 0 15px;
      border: 1px solid #E7E7E7;
      min-height: 140px;
      width: 100%;
      resize: none;
      margin-bottom: 10px;
      @media (min-width: @tablet-width) {
        margin-bottom: 0;
        min-height: 162px;
      }
      @media (min-width: @desktop-width) {
        margin-bottom: 16px;
      }
    }

  }
}
