.before-after {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 45px;
  padding-right: 39px;

  @media (min-width: @tablet-width) {
    align-content: flex-end;
    padding-left: 68px;
    padding-right: 70px;
    margin-top: 15px;
  }
  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: flex-end;
    margin-bottom: 11.4%;
    padding-right: 0;
    padding-left: 37px;
  }

  &__text {
    font-size: 16px;
    line-height: 20px;
    text-decoration: none;
    text-transform: uppercase;
    color: @black;
    cursor: pointer;
    z-index: 10;
    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
      background-image: url("../img/underline.png");
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: bottom center;
    }
  }

  &__sticky {
    position: relative;
    background-color: white;
    border-radius: 6px;
    width: 41%;
    height: 14px;
    margin-top: 2px;
    z-index: 10;
    border: 1px solid #EBEBEB;
    @media (min-width: @tablet-width) {
      background-color: #DCDCDC;
      width: 74.9%;
      height: 6px;
      margin-top: 13px;
    }
    @media (min-width: @desktop-width) {
      margin-top: 12px;
    }
  }

  &__white-circle {

    @media (min-width: @tablet-width) {
      position: absolute;
      top: -16px;
      left: 46.8%;
      width: 34px;
      height: 34px;
      background-color: white;
      border-radius: 50%;
      border: 1px solid #CDCDCD;
      cursor: pointer;
      z-index: 10;
      &:active {
        border: 1px solid #68B738;
      }
    }
  }

  &__green-slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 43%;
    background-color: @basic-green;
    margin-top: 4px;
    margin-left: 5px;
    height: 6px;
    border-radius: 6px;
    @media (min-width: @tablet-width) {
      position: absolute;
      width: 8px;
      height: 8px;
      top: 9px;
      left: 23%;
    }
  }
}
