.pet-info {

  &__label {
    width: 100%;
    min-height: 46px;
    align-content: space-between;
    margin-bottom: 13px;
    @media (min-width: @tablet-width) {
      min-height: 50px;
      margin-bottom: 21px;
    }
    @media (min-width: @desktop-width) {
      margin-bottom: 23px;
    }
  }

  &__text {
    display: block;
    width: max-content;
  }

  input {
    flex-grow: 1;
    margin-left: 5.5%;
    border: 1px solid #E7E7E7;
    outline: none;

    &:hover {
      border: 1px solid #CDCDCD;
    }

    &:focus,
    &:focus-within {
      border: 2px solid #444444;
    }

    @media (min-width: @tablet-width) {
      margin-left: 3%;
    }
  }
}
