.catalog-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  @media (min-width: @tablet-width) {
    justify-content: space-between;
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 24px;
    @media (min-width: @tablet-width) {
      width: 45.764%;
      margin-top: 17.5%;
      margin-bottom: 2.5%;
      padding-bottom: 39px;
      min-height: 371px;
      flex-direction: column;
      background: #f2f2f2;
      justify-content: flex-end;

      &--more {
        background: transparent;
        border: 1px solid #F2F2F2;
        justify-content: flex-start;
      }
    }
    @media (min-width: @desktop-width) {
      margin-top: 10%;
      margin-bottom: 1.8%;
      width: 20.1%;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 100vw;
      left: -20px;
      background-color: #f2f2f2;
      height: 1px;
      @media (min-width: @tablet-width) {
        display: none;
      }
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    picture {
      margin-top: 8px;
      align-self: flex-end;
      width: 50%;
      text-align: center;
      @media (min-width: @tablet-width) {
        position: absolute;
        top: -18%;
        left: 0;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: self-end;
        width: 100%;
        margin: 0;
        height: 203px;
      }
      @media (min-width: @desktop-width) {
        top: -19.4%;
      }
    }
  }

  &__description {
    width: 50%;
    @media (min-width: @tablet-width) {
      box-sizing: border-box;
      width: 100%;
      padding-left: 37px;
      padding-right: 37px;
      margin-top: 31px;
    }
    @media (min-width: @desktop-width) {
      padding-left: 42px;
      padding-right: 38px;
    }

    &--more {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      position: relative;
      text-align: center;
      min-height: 102px;
      justify-content: flex-end;
      width: 100%;
      @media (min-width: @tablet-width) {
        min-height: 278px;
        justify-content: center;
      }

      &::before,
      &::after {
        content: "";
        position: absolute;
        left: 44.8%;
        top: 19%;
        width: 30px;
        height: 2px;
        background: #d3d3d3;
        @media (min-width: @tablet-width) {
          left: 41.5%;
          top: 11.2%;
          width: 60px;
        }
        @media (min-width: @desktop-width) {
          left: 38%;
        }
      }

      &::after {
        transform: rotate(90deg);
      }
    }
  }

  &__name {
    margin-bottom: 13px;
    margin-top: 2px;
    font-size: 16px;
    line-height: 20px;
    color: @special-dark-grey;
    width: 60%;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      margin: 0 auto;
      text-align: center;
      font-size: 20px;
      line-height: 24px;
    }
    @media (min-width: @desktop-width) {
      width: 83%;
      padding-left: 9px;
      padding-bottom: 2px;
    }

    &--more {
      width: 100%;
      margin: 0;
      margin-bottom: 7px;
      @media (min-width: @tablet-width) {
        margin: 46px auto 0 auto;
        width: 56%;
      }
      @media (min-width: @desktop-width) {
        margin: 44px auto 24px;
        width: 100%;
        padding: 0;
      }
    }
  }

  &__info {
    padding: 0;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 9px;
    @media (min-width: @tablet-width) {
      margin-bottom: 20px;
      margin-top: 14px;
    }
  }

  &__info-link {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    @media (min-width: @tablet-width) {
      padding-bottom: 8px;
      padding-top: 6.5px;
      border-bottom: 1px solid #CDCDCD;
    }
  }

  &__title, &__decryption {
    font-family: "Arial";
    margin: 0;
    font-size: 12px;
    line-height: 16px;
    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 16px;
    }
  }

  &__button {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 13px;
    font-family: @font-family-base;
    color: @color-white;
    font-size: 16px;
    line-height: 20px;
    border: none;
    width: 100%;
    text-transform: uppercase;
    background-color: @basic-green;
    @media (min-width: @tablet-width) {
      width: 77%;
      align-self: center;
      margin-left: 3px;
    }
    @media (min-width: @desktop-width) {
      width: 69%;
      margin-left: 2px;
    }

    &--more {
      margin-top: 22px;
      background-color: #F2F2F2;
      color: @black;

      &:hover,
      &:focus {
        background-color: #EBEBEB;
      }

      &:active {
        background-color: #EBEBEB;
        color: fade(@black, 30%);
      }
    }
  }

  &__text-bottom {
    font-family: "Arial";
    font-size: 12px;
    line-height: 16px;
    margin: 0;
    @media (min-width: @tablet-width) {
      font-size: 16px;
      line-height: 18px;
      margin: 20px auto 0 auto;
      width: 69%;
    }
    @media (min-width: @desktop-width) {
      margin: 0;
      width: 100%;
    }
  }
}
