.footer-content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 16px;
  padding-bottom: 32px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    padding-top: 0;
    padding-bottom: 0;
  }

  &__logo {
    display: flex;
    align-self: center;
    margin-top: 23px;

    &:hover,
    &:focus {
      opacity: 80%;
    }

    &:active {
      opacity: 60%;
    }

    @media (min-width: @desktop-width) {
      margin-top: 17px;
      width: 13%;
    }
  }

  &__socials {
    padding: 0;
    margin-top: 32px;
    margin-bottom: 13px;
    padding-left: 70px;
    padding-right: 70px;
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    @media (min-width: @tablet-width) {
      align-items: center;
      border: none;
      width: 26%;
      padding: 0;
      margin-top: 20px;
      justify-content: end;
    }
    @media (min-width: @desktop-width) {
      justify-content: space-between;
      width: 13%;
      margin-top: 30px;
    }
  }

  &__item {
    align-self: flex-end;
    padding: 17px 6px;
    @media (min-width: @tablet-width) {
      padding: 27px 13px;
    }
  }

  &__link {
    svg {
      fill: transparent;
      stroke: @dark-grey;

      &:hover, focus {
        stroke: #68B738;
      }

      &:active {
        opacity: 30%;
      }
    }

    @media (min-width: @tablet-width) {
      &--vk {
        width: 30px;
        height: 22px;
      }

      &--insta {
        width: 23px;
        height: 22px;
      }

      &--facebook {
        width: 28px;
        height: 33px;
      }
    }
    @media (min-width: @desktop-width) {
      &--vk {
        width: 29px;
        height: 33px;
      }

      &--insta {
        height: 33px;
      }
    }
  }

  &__create-by {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    font-family: "Arial";
    font-size: 16px;
    line-height: 18px;
    color: @dark-grey;
    text-decoration: none;

    p {
      padding-right: 23px;
    }

    &:hover, focus {
      stroke: #68B738;
    }

    &:active {
      opacity: 30%;
    }

    @media (min-width: @tablet-width) {
      align-self: center;
      margin-top: 16px;
    }

    svg {
      color: @black;
      width: 27px;
      height: 34px;
    }
  }
}

