.main-section {
  display: flex;
  position: relative;
  min-height: 478px;
  flex-direction: column;
  z-index: 10;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-size: 100% 63%;
  background-repeat: no-repeat;
  background-position: top center;
  @media (min-width: @tablet-width) {
    background: none;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    position: relative;

    @media (min-width: @tablet-width) {
      padding-left: 30px;
      padding-right: 30px;
      margin: 0 auto;
      max-width: 1440px;
      min-width: 320px;
      box-sizing: border-box;
    }
    @media (min-width: @desktop-width) {
      padding-left: 190px;
      padding-top: 46px;
      margin: 0;
      width: 100%;
      min-height: 560px;
    }
  }

  &__picture-can {
    z-index: 10;
    text-align: center;
    width: 100%;
    height: 270px;
    @media (min-width: @tablet-width) {
      height: 609px;
    }
    @media (min-width: @desktop-width) {
      position: absolute;
      top: 1%;
      right: 23%;
      width: 552px;
      height: 499px;
    }
  }

  &__description {
    padding-right: 35px;
    padding-left: 35px;
    z-index: 10;
    @media (min-width: @tablet-width) {
      padding-left: 87px;
      padding-right: 120px;
    }
    @media (min-width: @desktop-width) {
      padding: 0;
      width: 40%;
    }
  }

  &__title {
    font-weight: @font-weight-default;
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 26px;
    margin-top: 27px;
    text-align: center;
    color: @color-white;
    @media (min-width: @tablet-width) {
      margin-top: 42px;
      color: @black;
      font-size: 60px;
      line-height: 60px;
      text-align: left;
    }
  }

  &__subtitle {
    padding-bottom: 18px;
    color: @color-white;
    font-size: 14px;
    line-height: 14px;
    font-weight: @font-weight-default;
    text-align: center;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      padding-bottom: 0;
      margin-top: 42px;
      margin-bottom: 19px;
      font-size: 20px;
      line-height: 20px;
      color: @black;
      text-align: left;
    }
  }

  &__button {
    margin: 0 auto;
    text-decoration: none;
    color: @color-white;
    font-size: 14px;
    line-height: 18px;
    padding: 13px 20px 11px;
    width: 75%;
    text-align: center;
    background: @basic-green;
    text-transform: uppercase;
    @media (min-width: @tablet-width) {
      position: absolute;
      top: 32%;
      left: 113px;
      width: 26.5%;
      z-index: 11;
      padding: 17px 20px;
      font-size: 20px;
      line-height: 20px;
    }
    @media (min-width: @desktop-width) {
      left: 190px;
      top: 57.5%;
      width: 15%;
      font-size: 20px;
      line-height: 20px;
      padding: 16px 13px;
    }
  }
}
