.programms {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  list-style-type: none;
  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 82px;
  }

  &__description {
    display: flex;
    flex-wrap: wrap;
    box-sizing: border-box;
    flex-direction: row;
    padding-left: 20px;
    padding-right: 20px;
    @media (min-width: @tablet-width) {
      width: 69%;
      padding: 53px 53px 37px;
    }
    @media (min-width: @desktop-width) {
      width: 100%;
    }
  }

  &__item {
    position: relative;
    background-color: @basic-grey;
    width: 100%;
    margin-top: 22px;
    @media (min-width: @tablet-width) {
      margin-top: 0;
      margin-bottom: 28px;
    }
    @media (min-width: @desktop-width) {
      width: 46.8%;
    }

    &::before {
      content: "";
      position: absolute;
      width: 50px;
      height: 51px;
      border-radius: 50%;
      top: 23px;
      left: 23px;
      background: @basic-green;
      @media (min-width: @tablet-width) {
        width: 200px;
        height: 200px;
        left: 63%;
        top: 25%;
      }
      @media (min-width: @desktop-width) {
        width: 100px;
        height: 100px;
        left: 9.2%;
        top: 13%;
      }
    }

    &--cat {
      position: absolute;
      top: 21px;
      left: 30px;
      z-index: 2;
      width: 36px;
      height: 50px;
      @media (min-width: @tablet-width) {
        width: 140px;
        height: 192px;
        top: 22%;
        left: 67%;
      }
      @media (min-width: @desktop-width) {
        width: 70px;
        height: 97px;
        top: 15%;
        left: 12%;
      }
    }

    &--lion {
      position: absolute;
      top: 34px;
      left: 14px;
      z-index: 2;
      width: 66px;
      height: 28px;
      @media (min-width: @tablet-width) {
        width: 268px;
        height: 112px;
        top: 37%;
        left: 58%;
      }
      @media (min-width: @desktop-width) {
        width: 135px;
        height: 56px;
        top: 19%;
        left: 6%;
      }
    }
  }

  &__title {
    text-transform: uppercase;
    color: @black;
    font-size: 24px;
    line-height: 37px;
    font-weight: @font-weight-default;
    margin-top: 30px;
    margin-bottom: 17px;
    padding-left: 72px;
    @media (min-width: @tablet-width) {
      font-size: 36px;
      line-height: 36px;
      text-align: left;
      padding-left: 0;
      margin-top: 23px;
      margin-bottom: 10px;
    }
    @media (min-width: @desktop-width) {
      padding-left: 35%;
    }
  }

  &__text {
    font-family: "Arial";
    font-size: 14px;
    line-height: 18px;
    color: @dark-grey-second;
    margin-bottom: 20px;
    padding-bottom: 22px;
    border-bottom: 1px solid #D9D9D9;
    @media (min-width: @tablet-width) {
      padding-bottom: 31px;
      border: none;
      font-size: 16px;
      line-height: 24px;
      &--second {
        width: 90%;
        @media (min-width: @desktop-width) {
          width: 100%;
        }
      }
    }
    @media (min-width: @desktop-width) {
      margin-top: 65px;
      padding-bottom: 6px;
    }
  }

  &__link {
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 16px;
    color: @black;
    padding-bottom: 20px;
    
    &:active {
      opacity: 30%;

      &::after {
        opacity: 30%;
      }
    }

    &::after {
      content: "";
      position: absolute;
      width: 32px;
      height: 12px;
      top: 2px;
      right: -50px;
      background: url("../img/icons/icon_arrow.svg");
      @media (min-width: @tablet-width) {
        top: 10px;
      }

      @media (min-width: @desktop-width) {
        right: -61px;
      }
    }

    @media (min-width: @tablet-width) {
      font-size: 20px;
      line-height: 30px;
    }
  }
}

