.product-info {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: relative;
  @media (min-width: @tablet-width) {
    min-height: 755px;
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: 0;
      left: 0;
      background: #E6E6E6;
    }
  }
  @media (min-width: @desktop-width) {
    min-height: 438px;
    &::after {
      display: none;
    }
  }

  &__title {
    margin-top: 27px;
    margin-bottom: 27px;
    font-size: 36px;
    line-height: 40px;
    color: @black;
    @media (min-width: @tablet-width) {
      font-size: 60px;
      line-height: 60px;
      margin-top: 18px;
    }
    @media (min-width: @desktop-width) {
      font-size: 58px;
      line-height: 60px;
      margin-top: 44px;
    }
  }

  &__list {
    list-style-type: none;
    margin: 0;
    padding: 0 0 34px;
    @media (min-width: @tablet-width) {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 57px;
      row-gap: 39px;
      padding: 0;
    }
    @media (min-width: @desktop-width) {
      grid-template-columns: repeat(4, 1fr);
      width: 100%;
      column-gap: 80px;
    }
  }

  &__item {
    position: relative;
    padding-left: 80px;
    min-height: 66px;
    font-family: "Arial";
    font-size: 14px;
    line-height: 18px;
    color: @dark-grey-second;

    br {
      display: none;
      @media (min-width: @tablet-width) {
        display: inline-block;
      }
    }

    @media (min-width: @tablet-width) {
      display: grid;
      padding-left: 0;
      font-size: 16px;
      line-height: 24px;
      min-height: 280px;
      background-repeat: no-repeat;
      background-position: top right;
      p {
        align-self: center;
        margin-top: 120px;
        width: 76%;
        @media (min-width: @desktop-width) {
          width: 100%;
        }
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: -3px;
      left: 1px;
      background: @basic-green;
      width: 60px;
      height: 60px;
      @media (min-width: @tablet-width) {
        width: 80px;
        height: 80px;
        top: 16%;
        left: 0;
      }

    }

    &::after {
      content: "";
      position: absolute;
      top: 12px;
      left: 17px;
      z-index: 2;
      @media (min-width: @tablet-width) {
        top: 24%;
        left: 25px;
      }
    }

    &--leaf {
      &::after {
        background: url("../img/icons/icon_leaf.svg");
        width: 30px;
        height: 32px;
      }

      @media (min-width: @tablet-width) {
        background-image: url("../img/number-one.png");
        background-position: 58% 83%;
      }
      @media (min-width: @desktop-width) {
        background-position: 73% 83%;
      }
    }

    &--tub {
      &::after {
        background: url("../img/icons/icon_tub.svg");
        width: 29px;
        height: 31px;
      }

      @media (min-width: @tablet-width) {
        background-image: url("../img/number-two.png");
        background-position: 58% 83%;
      }
      @media (min-width: @desktop-width) {
        background-position: 93% 83%;
      }
    }

    &--eat {
      &::after {
        background: url("../img/icons/icon_eat.svg");
        width: 25px;
        height: 30px;
      }

      @media (min-width: @tablet-width) {
        background-image: url("../img/number-three.png");
        background-position: 58% 83%;
      }
      @media (min-width: @desktop-width) {
        background-position: 93% 93%;
      }
    }

    &--alarm {
      &::after {
        background: url("../img/icons/icon_alarm.svg");
        width: 32px;
        height: 33px;
      }

      @media (min-width: @tablet-width) {
        background-image: url("../img/number-four.png");
        background-position: 61% 83%;
      }
      @media (min-width: @desktop-width) {
        background-position: 93% 83%;
      }
    }
  }
}
